.coupon-alldiscount {
  display: none !important;
}

.order-sidebar {
  margin-top: 35px;
  background-color: #fff;
  box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  padding: 35px 25px;
  margin-bottom: 15px;
}

.order-sidebar .order-sidebar_title {
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 10px;
}

.order-sidebar .charter-time_multy {
  display: flex;
  border-top: 2px solid #E9E9E9;
  border-bottom: 2px solid #E9E9E9;
  cursor: pointer;
}

.order-sidebar .charter-time_multy span {
  display: block;
  width: 50%;
  padding: 15px;
  font-weight: 500;
  color: #1f283c;
}

.order-sidebar .charter-time_multy span:first-child {
  border-right: 2px solid #E9E9E9;
  border-left: 2px solid #E9E9E9;
}

.order-sidebar .charter-time_multy span:last-child {
  border-right: 2px solid #E9E9E9;
  position: relative;
}

.order-sidebar .charter-time_multy span:last-child:after {
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  right: 14px;
  background: transparent url("../img/filter-day-icon.svg") center no-repeat;
  background-size: contain;
}

.order-sidebar .charter-input {
  padding: 15px;
  height: 56px;
  border: 2px solid #E9E9E9;
  position: relative;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  color: #1F283C;
  cursor: pointer;
}

.order-sidebar .charter-input span {
  font-weight: 400;
  color: #797C84;
}

.order-sidebar .charter-input:after {
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  background: transparent url("../img/arrow-down.svg") center no-repeat;
  background-size: contain;
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  right: 14px;
}

.order-sidebar .charter-input:first-child {
  border-radius: 4px 4px 0 0;
  border-bottom: 0;
}

.order-sidebar .charter-input.charter-passenger .peoples-wrap_inner {
  bottom: -235px;
}

.order-sidebar .charter-input.charter-time {
  border-radius: 0;
}

.order-sidebar .charter-input.charter-time:after {
  background: transparent url("../img/filter-day-icon.svg") center no-repeat;
  background-size: contain;
}

.order-sidebar .charter-input:last-of-type {
  border-radius: 0 0 4px 4px;
  border-top: 0;
}

.order-sidebar .charter-time.active:after {
  background: transparent url("../img/cross.svg") center no-repeat;
  background-size: 25px;
}

.order-sidebar .charter-options {
  margin-top: 25px;
}

.order-sidebar .charter-option_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
}

.order-sidebar .charter-option_row:last-child {
  margin-bottom: 0;
}

.order-sidebar .charter-option_row p {
  font-size: 17px;
  line-height: 24px;
}

.order-sidebar .charter-option_row .medium-text {
  font-weight: 500;
  color: #1F283C;
}

.order-sidebar .charter-option_row .charter-row_price {
  color: #23A8CD;
  font-weight: 500;
}

.order-sidebar .to-checkout_button {
  display: block;
  text-align: center;
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  padding: 15px 0;
  border-radius: 12px;
  overflow: hidden;
  background-color: #23A8CD;
  box-shadow: 0 8px 24px 0 rgba(24, 120, 147, 0.24);
  margin-top: 25px;
}

.order-sidebar .to-checkout_button.disabled {
  background: #A6AEBE;
  box-shadow: 0 8px 24px 0 rgba(166, 174, 190, 0.24);
}

.order-sidebar .to-checkout_button.disabled:hover {
  background-color: #A6AEBE;
}

.order-sidebar .to-checkout_button:hover {
  color: #fff !important;
  background-color: #1c84a1;
}

.order-sidebar .custom-remove_button {
  background: #e8e8e8;
  color: #000;
  box-shadow: none;
}

.order-sidebar .custom-remove_button:hover {
  color: #000 !important;
}

.sidebar-bottom_text {
  font-size: 13px;
  line-height: 21px;
  text-align: center;
  max-width: 320px;
  margin: 0 auto;
}

.charter-type_dropdown {
  display: none;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.08);
  position: absolute;
  padding: 15px;
  z-index: 10;
  width: 100%;
  top: 100%;
  left: 0;
}

.charter-type_dropdown div {
  margin-bottom: 10px;
}

.charter-type_dropdown div:last-child {
  margin-bottom: 0;
}

.charter-type_dropdown .charter-type_item-Fleets {
  display: none;
}

@media screen and (max-width: 500px) {
  .order-sidebar {
    margin-top: 0;
  }
}

